import { lazy, Suspense } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { Layout } from 'components';
//import { Layout, Cast, Reviews } from 'components';
//import { HomePage, MovieDetailsPage, MoviesPage } from 'pages';

const reateChankPage = pageName => {
  return lazy(() =>
    import(`../../pages/${pageName}.jsx`).then(module => ({
      default: module[pageName],
    }))
  );
};
const reateChankComponent = componentName => {
  return lazy(() =>
    import(`../${componentName}/${componentName}.jsx`).then(module => ({
      default: module[componentName],
    }))
  );
};

const MoviesPage = reateChankPage('MoviesPage');
const HomePage = reateChankPage('HomePage');
const MovieDetailsPage = reateChankPage('MovieDetailsPage');
const Cast = reateChankComponent('Cast');
const Reviews = reateChankComponent('Reviews');

export const App = () => {
  return (
    // <div
    //   style={{
    //     height: '100vh',
    //     display: 'flex',
    //     justifyContent: 'center',
    //     alignItems: 'center',
    //     fontSize: 40,
    //     textTransform: 'uppercase',
    //     color: '#010101',
    //   }}
    // >
    //   React homework template
    // </div>
    <Suspense fallback="">
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index element={<HomePage />} />
          <Route path="movies" element={<MoviesPage />} />
          <Route path="movies/:movieId" element={<MovieDetailsPage />}>
            <Route path="cast" element={<Cast />} />
            <Route path="reviews" element={<Reviews />} />
          </Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Route>
      </Routes>
    </Suspense>
  );
};
